import "./styles.css";
import { useState, useEffect } from "react";
import axios from "axios";

//https://randomuser.me/api

const fetchRandomData = (pageNumber) => {
  return axios
    .get(`https://randomuser.me/api?page${pageNumber}`)
    .then(({ data }) => {
      console.log(data);
      return data;
    })
    .catch((err) => {
      console.log(err);
    });
};
const getFullUserName = (userInfo) => {
  const {
    name: { first, last }
  } = userInfo;
  return `${first} ${last}`;
};
const getUserPicture = (userInfo) => {
  const { picture } = userInfo;
  return `${picture.thumbnail}`;
};

export default function App() {
  const [friends, setFriends] = useState(1);
  const [randomUserDataJSON, setRandomUserDataJSON] = useState("");
  const [userInfo, setUserInfo] = useState([]);
  const [nextPageNumber, setNextPageNumber] = useState(1);
  const fetchNextUser = () => {
    fetchRandomData(nextPageNumber).then((randomData) => {
      const nextUserInfos = [...userInfo, ...randomData.results];
      // setRandomUserDataJSON(JSON.stringify(randomData,null,2) || "no user data found")
      setUserInfo(nextUserInfos);
      setNextPageNumber(randomData.info.page + 1);
    });
  };

  useEffect(() => {
    // fetchRandomData(nextPageNumber).then((randomData)=>{
    //   console.log("foo")
    //   setRandomUserDataJSON(JSON.stringify(randomData,null,2) || "no user data found")
    //   setUserInfo(randomData.results)
    //   setNextPageNumber(randomData.info.page +1)
    // })
    fetchNextUser();
  }, []);

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Hii I have {friends} Friends</h2>
      <button
        onClick={() => {
          setFriends((friends) => friends + 1);
          console.log("foo");
        }}
      >
        Add a friend
      </button>
      <button onClick={fetchRandomData}>Fetch user data</button>
      <button onClick={fetchNextUser}>Fetch next User</button>
      {/* <pre>
      <p>{randomUserDataJSON}</p></pre> */}
      {userInfo.map((userInfo, idx) => (
        <div key={idx}>
          <p>{getFullUserName(userInfo)}</p>
          <img src={getUserPicture(userInfo)} />
        </div>
      ))}
    </div>
  );
}
